@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  background-image: url(/static/media/cosmos-pexels.2d969e3e.jpg);
}

.App {
  font-family: "Open Sans Condensed", sans-serif;
  text-align: center;
  color: whitesmoke;
}

.img {
  position: absolute;
  width: 100%;
  height: 100%;
}
h1,
h2 {
  font-size: 1.2em;
  text-align: center;
  text-shadow: 0 22px 50px rgba(14, 21, 47, 0.2),
    0 8px 20px rgba(14, 21, 47, 0.2);
  position: relative;
}

h3 {
  position: fixed;
  bottom: 1em;
  left: 50%;
  transform: translate(-50%, -50%);
}

img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
  box-shadow: 0 22px 50px rgba(14, 21, 47, 0.2),
    0 8px 20px rgba(14, 21, 47, 0.2);
  margin-bottom: 3em;
}

a {
  text-decoration: none;
  color: whitesmoke;
}

p {
  text-align: center;
}

strong {
  text-decoration: underline;
}

.text {
  background: rgba(0, 0, 0, 0.2);
  z-index: 1;
  flex-grow: 1;
  position: absolute;
  text-align: justify;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 4%;
  color: white;
  width: 50%;
  border-radius: 10px;
  padding: 0;
}

.credit {
  font-style: italic;
  text-align: center;
}

span[data-descr] {
  position: relative;
  cursor: help;
}

span[data-descr]:hover::after {
  content: attr(data-descr);
  position: absolute;
  left: 55%;
  top: 12em;
  transform: translate(-50%, -50%);
  border: 1px #aaaaaa solid;
  border-radius: 10px;
  background-color: #333;
  color: whitesmoke;
  font-size: 0.8em;
  padding: 1em;
  width: 290px;
  text-align: justify;
}

